<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示平台定义的运单模板信息</li>
      <li>启用后的模板可以供商家打快速设置运单模板</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>模板名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/waybillTemplate/save'"  icon="add" type="success" size="small" @click="$routerLink('/waybillTemplate/save','save')">新增</cms-button>
      <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="name" label="模板名称" min-width='120'></el-table-column>
      <el-table-column prop="logisticsCompany.cnName" label="物流公司" min-width="160"></el-table-column>
      <el-table-column prop="isEnable" label="是否启用" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.isEnable" :id='scope.row.id' @change="setDisable"></cms-switch>
        </div>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button v-prem="'/waybillTemplate/edit'" icon='edit' size='small' type='info' @click.stop="$routerLink('/waybillTemplate/edit','update',{id:scope.row.id,logisticsCompanyId:scope.row.logisticsCompany.id})">编辑</cms-button>
          <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  waybillTemplatePage,
  waybillTemplateDelete,
  waybillTemplateEnable,
  waybillTemplateDisable
} from '@/api/setting/waybillTemplate.js'
var defaultData = {
  waybillType: '1',
  name: '',
  size: 10,
  page: 1
}
export default {
  name: 'waybillTemplateList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(waybillTemplateDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(waybillTemplateDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    setDisable(status, id) {
      if (!status) {
        waybillTemplateDisable({ ids: [id] }).then(res => {
          // 启用
          if (res.code === 200) {
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage(res.messsage)
          }
        })
      } else {
        waybillTemplateEnable({ ids: [id] }).then(res => {
          // 禁用
          if (res.code === 200) {
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage(res.messsage)
          }
        })
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(waybillTemplatePage, this.queryParams)
    }
  },
  created() {
    // 首次触发

    this.getTableData(waybillTemplatePage, this.queryParams)
  }
}
</script>

<style>
</style>



